<template>
  <div id="app">
    <!-- @status-Todos="status" -->
    <todosHeader
      @todo-header="keyValue"
      :todoHeader="statusTodos"
      @todo-Status="status"
      :todos="todos"
    />
    <todosList
      :todoLisi="todos"
      :newLiActive="newLi[active]"
      @todo-List="toggleCheck"
      @todo-listDel="DelFunc"
      @open-Bar="editmodify"
      @list-Itemup="downEnter"
    />

    <todosFuncBar
      :todoBar="todos"
      @Del-All="DelAll"
      @todo-Choose="choose"
      :active="active"
    />
  </div>
</template>

<script>
import todosHeader from "./components/todosHeader.vue";
import todosList from "./components/todosList.vue";
import todosFuncBar from "./components/todosFuncBar.vue";
//App
// todosHeader
// todosList
// todosItem
// todosFuncBar
// todosFooter
export default {
  name: "App",
  data() {
    return {
      todos: [],
      active: "",
      newLi: { All: "", Active: "", Completed: "" },
    };
  },
  created() {
    this.todos = JSON.parse(window.localStorage.getItem("todos")) ?? [];
    this.active = JSON.parse(window.localStorage.getItem("active")) ?? "All";
    this.newLi.All = this.allFunc;
    this.newLi.Active = this.activeFunc;
    this.newLi.Completed = this.completedFunc;
  },
  computed: {
    allFunc: function () {
      //全部的数据
      return this.todos;
    },
    activeFunc: function () {
      // return this.todos.filter((o) => o.finish == false);
      return this.todos.filter((o) => !o.finish);
    },
    completedFunc: function () {
      //有true的结果
      // return this.todos.filter((o) => o.finish == true);
      return this.todos.filter((o) => o.finish );
      // return this.todos,filter
    },
    statusTodos: function () {
      return this.todos.every((o) => o.finish);
    },
  },
  components: {
    todosHeader,
    todosList,
    todosFuncBar,
    // todosFooter,
  },
  methods: {
    keyValue: function (text) {
      // console.log(123,res);
      // 注入 id text finish
      this.todos = [
        ...this.todos,
        {
          id: this.todos.length
            ? Math.max(
                ...this.todos.map((o) => {
                  return o.id;
                })
              ) + 1
            : 1,
          text,
          finish: false,
          openText: false,
        },
      ];
    },
    toggleCheck: function (item) {
      this.todos = this.todos.map((o) => {
        if (o.id == item.id) {
          return {
            ...o,
            finish: !o.finish,
          };
        } else {
          return o;
        }
      });
    },
    DelFunc: function (item) {
      this.todos = this.todos.filter((o) =>o.id !== item.id);
    },
    status: function (item) {
      this.todos = this.todos.map((o) => ({...o,finish: item,}) )
    },
    DelAll: function () {
      this.todos = this.activeFunc;
    },
    choose: function (item) {
      // console.log(item)
      this.active = item;
    },
    editmodify: function (res) {
      // console.log(res);
      // ({...o,openText:!res.openText})
      this.todos = this.todos.map((o) => {
        return o.id === res.id
          ? { ...o, openText: !res.openText }
          : { ...o, openText: false };
      });
    },
    downEnter: function ({ item, event }) {
      console.log( item,event)
      this.todos = this.todos.map((o) => {
        if (o.id == item.id) {
          return {
            ...o,
            openText: !item.openText,
            text:event.target.value
          };
        }else{
          return o
        }
      });
    },
  },
  watch: {
    todos: function (res) {
      console.log("监听todos状态:true");
      let nTodos = res.map((o) => ({ ...o, openText: false }));
      window.localStorage.setItem("todos", JSON.stringify(nTodos)) ?? [];
      this.newLi.All = this.allFunc;
      this.newLi.Active = this.activeFunc;
      this.newLi.Completed = this.completedFunc;
    },
    active: function (res) {
      console.log(res + "当前状态:true");
      window.localStorage.setItem("active", JSON.stringify(res));
    },
  },
};
</script>

