<template>
  <div class="header">
    <div class="title">todos</div>
    <div class="top">
      <div class="left">
        <i class="fa fa-angle-down" :class="todoHeader?'change':''" aria-hidden="true"></i>
        <input
          v-show="todos.length > 0 ? true : false"
          type="checkbox"
          :checked="todoHeader"
          @change="statusTodos"
        />
      </div>
      <input
        type="text"
        v-model="value"
        @keyup.enter="addItem()"
        placeholder="What needs to be done?"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["todoHeader", "todos"],
  data() {
    return {
      value: "",
    };
  },
  methods: {
    addItem: function () {
      //   console.log(this.value)
      this.value && this.$emit("todo-header", this.value);
      this.value = "";
    },
    statusTodos: function (event) {
      // console.log(event.target.checked);
      this.$emit("todo-Status", event.target.checked);
    },
  },
};
</script>

<style lang="less">
.title {
  font-size: 100px;
  text-align: center;
  color: rgb(109, 187, 255);
  font-weight: lighter;
}
.header {
  display: flex;
  flex-direction: column;
  /* margin: 10px 0; */
}
.top {
  padding: 10px;
  display: flex;
  justify-content: center;
  border: 1px solid rgb(255, 255, 255);
}
.top .left{
  position: relative;

}
.top .left i{
  position: absolute;
  color: rgb(0, 0, 0);
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  font-size: 30px;
  pointer-events: none;
  transition: all .3s;
}
.top .left i.change{
  z-index: -1;
 
  color: rgb(0, 136, 255);
}
.top  input[type="checkbox"] {
  position: relative;
  z-index: 10;
  opacity: 0;
  width: 50px;
  height: 100%;
}
.top > input::placeholder {
  font-style: oblique;
  font-weight: lighter;
  color: #ccc;
}
.top > input[type="text"] {
  width: 100%;
  padding: 20px;
  margin-left: 10px;
  border: 1px solid rgb(255, 255, 255);
  outline: 0;
  font-size: 18px;
}
</style>
