<template>
  <div class="thing" v-show="todoBar.length > 0 ? true : false">
    <!-- <p>{{todoBar}}</p> -->
    <div class="lastthing">剩余{{ lassThing.length }}件事</div>
    <ul class="incident">
      <li
        v-for="key in newLi"
        @click="choose(key)"
        :class="active == '' ? ('All' == key ? 'active' : '') : active == key ? 'active' : ''"
        :key="key"
      >
        {{ key }}
      </li>
    </ul>
    <div class="delAll" @click="Del">删除全部已完成</div>
  </div>
</template>
<script>
export default {
  props: {
    todoBar: Array,
    active: String,
  },
  computed:{
    lassThing:function(){
          // console.log(this.todos.filter((o)=> o.finish == false));
          return this.todoBar.filter((o)=>o.finish == false)
        }
  },
  data: function () {
    return {
      newLi: ["All", "Active", "Completed"],
    };
  },
  methods: {
    Del: function () {
      this.$emit("Del-All");
    },
    choose: function (res) {
      // console.log(res);
      this.$emit("todo-Choose", res);
    },
  },
};
</script>

<style>
.thing {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  box-sizing: border-box;
}
.thing .incident {
  list-style: none;
  display: flex;
  align-items: center;
}
.thing .incident li {
  margin-left: 10px;
  transition: all .3s;
  cursor: pointer;
}
.thing .incident li.active {
  padding: 10px;
  border-radius: 5px;
  background-color: rgb(109, 187, 255);
  color: #fff;
}
</style>
