<template>
  <div class="content">
    <li
      class="item"
      v-for="item in newLiActive"
      @dblclick="openText(item)"
      :key="item.id"
    >
      <div class="itemBox">
        <div class="item-Check">
          <input
            type="checkbox"
            @change="toggleCheck(item)"
            @dblclick.stop="nullFunc()"
            :checked="item.finish"
          />
        </div>
        <div v-if="!item.openText" class="text">
          <span>{{ item.text }}</span>
        </div>
        <input
          type="text"
          v-if="item.openText"
          @keyup.enter="up(item, $event)"
          v-model="item.text"
        />
      </div>
      <i class="fa fa-times" aria-hidden="true" @click="todosDelete(item)"></i>
    </li>
  </div>
</template>
<script>
export default {
  props: {
    todoItem: Array,
    newLiActive: Array,
  },

  methods: {
    toggleCheck: function (item) {
      this.$emit("todo-Item", item);
    },
    todosDelete: function (item) {
      this.$emit("todo-ItemDel", item);
    },
    openText: function (status) {
      this.$emit("open-ListText", status);
    },
    up: function (item, $event) {
      let event = $event;
      this.$emit("ltem-Up", { item, event });
    },
    nullFunc: function () {
      return;
    },
  },
};
</script>

<style lang="less">
  .content{
    box-shadow: 0 1px 1px rgb(0 0 0 / 20%), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgb(0 0 0 / 20%), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgb(0 0 0 / 20%);
  }
.item {
  display: flex;
  align-items: center;
  padding: 20px 10px;
  font-size: 18px;
  border: 1px solid rgb(255, 255, 255);
  margin: clac(1px * 50%) 0;
  justify-content: space-between;
  .itemBox {
    display: flex;
    width: 100%;
    margin-right: 20px;
    align-items: center;
    font-size: 24px;
    .item-Check {
      display: flex;
      i {
      }
      input {
        width: 50px;
        height: 50px;
        margin-right: 10px;
      }
    }
    .text {
      span {
        margin-left: 10px;
        padding: 20px 0;
      }
    }
    input {
      width: 100%;
      padding: 10px;
      border: 1px solid rgba(255, 255, 255, 0.813);
      font-size: 24px;
      box-shadow: inset 0 0 10px 0 rgba(157, 157, 157, 0.482);
      outline: 0;
    }
  }
  i {
    font-size: 24px;

  }
}
</style>
